<template>

    <div class="sm-side-nav">
        <div class="row">
            <span style="font-size:30px;cursor:pointer" @click="openNav()">&#9776;</span>
        </div>
        <div id="mySidenav" class="row sidenav">
                <a href="javascript:void(0)" class="closebtn" @click="closeNav()">&times;</a>
                <!-- <div class="title p-3">K-Work Tech</div> -->
                <div class="sub-heading  pl-4 pt-5">
                     <!-- <div @click="logout()">Logout</div> -->
                     <!-- <div @click="getLogin()" v-if="isLoginUser == null">Login</div> -->
                     <div @click="logout()" v-if="isLoginUser=='admin'">Logout</div>
                        <router-link to="/admin/productlist" v-if="isLoginUser=='admin'">
                            <a v-bind:class="[isProductList  ? 'blue' : 'gray']" @click="toggleMainCat(0)">admin product list</a>
                        </router-link>
                        <br/>
                        <router-link to="/admin/product/1" v-if="isLoginUser=='admin'">
                            <a v-bind:class="[isCreateProd  ? 'blue' : 'gray']" @click="toggleMainCat(-1)">Create product</a>
                        </router-link>
                        
                    <div class="font-weight-bold" v-on:click="$emit('callProductList', 0,0)">
                        <a href="javascript:void(0)" v-bind:class="[isClearAll  ? 'blue' : 'gray']" @click="toggleMainCat('all')"> ALL</a>
                    </div>
                    <!-- <ul class="list-group" v-for="item in categoryList" :key="item.main_category_id">
                        <li class="list-group-item text-uppercase  p-0" > 
                            <span class="font-weight-bold" v-on:click="$emit('callProductList', item.main_category_id, 0)">
                             <a href="javascript:void(0)" v-bind:class="[item.main_category_id == isActive  ? 'blue' : 'gray']" @click="toggleMainCat(item.main_category_id)">{{item.main_category_name}}</a>
                            </span>  
                            <ul class="list-group" v-for="subitem in item.sub_category" :key="subitem.subcategory_id"> 
                                <li class="list-group-item text-uppercase py-1" v-on:click="$emit('callProductList', item.main_category_id, subitem.subcategory_id)">
                                    <a href="javascript:void(0)" v-bind:class="[(subitem.subcategory_id == isSubActive && item.main_category_id == isActive) ? 'blue' : 'gray']" @click="toggleSubCat(subitem.subcategory_id,item.main_category_id)"> {{subitem.subcategory_name}}</a>
                                </li>
                            </ul>
                        </li>
                    </ul> -->
                    <ul class="list-group" v-for="item in categoryList" :key="item.main_category_id">
                        <li class="list-group-item text-uppercase  p-0" > 
                            <span class="font-weight-bold" href="javascript:void(0)" 
                            v-on:click="$emit('callProductList', item.main_category_id, 0)" >
                                <a href="javascript:void(0)" v-bind:class="[item.main_category_id == isActive  ? 'blue' : 'gray']" @click="toggleMainCat(item.main_category_id)">{{item.main_category_name}}</a>
                            </span>  
                            <ul class="list-group" v-for="subitem in item.sub_category" :key="subitem.subcategory_id"> 
                                <li class="list-group-item subCategory-text text-uppercase py-1" 
                                    v-on:click="$emit('callProductList', item.main_category_id, subitem.subcategory_id)">
                                <a href="javascript:void(0)" v-bind:class="[(subitem.subcategory_id == isSubActive && item.main_category_id == isActive) ? 'blue' : 'gray']" @click="toggleSubCat(subitem.subcategory_id,item.main_category_id)"> {{subitem.subcategory_name}}</a>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </div>
        </div>
    </div>

</template>
<script>

import Vue from 'vue'
import ProductService from '../../shared/services/product.service'
   
    const productService = new ProductService();

    export default Vue.extend ({  
        data() {
            return {
                categoryList :{},
                subcategoriesList:{},
                isLoginUser:String,
                maincat:Boolean,
                isActive:Number,
                isSubActive:Number,
                isCreateProd:false,
                isProductList:false,
                isClearAll:false
            }
        },
        created() {
            this.isLogin();
            productService.getCategoriesList()
            .then(response => {
            this.categoryList = response.data.output.success[0].data
             this.categoryList.forEach((element, index) => {
                    var tempObj = JSON.parse(this.categoryList[index].sub_category);
                    this.subcategoriesList[element['sub_category']]=[];
                        for(var i in tempObj){
                            var obj = {};
                            obj['subcategory_id'] = i;
                            obj['subcategory_name'] = tempObj[i];
                            this.subcategoriesList[element['sub_category']].push(obj);
                        }
                    this.categoryList[index]['sub_category']=this.subcategoriesList[element['sub_category']]
                }); 
            })
            .catch(e => {
            this.errors.push(e)
            })
        },
        methods: {
            toggleMainCat(event){
                this.isActive = event
                console.log("is active event event ",this.isActive)
                if(event == 0)
                this.isProductList = true;    
                // else if(event !=0) 
                else
                this.isProductList = false; 
                if(event == -1)
                this.isCreateProd = true ;
                // else  if( event !=-1)
                else
                this.isCreateProd = false;
                if (event == 'all')
                this.isClearAll = true
                else this.isClearAll =false
                this.isSubActive = 0
            },
            toggleSubCat(event,mainCat){
                this.isSubActive = event
                this.isActive = mainCat
                // this.isActive = null
            },
            openNav() {
                document.getElementById("mySidenav").style.width = "250px";
            },
            closeNav() {
                document.getElementById("mySidenav").style.width = "0";
            },
            getLogin(){
                // window.location.href="/admin/login"
                this.$router.push({path:'/admin/login'})
            },
            isLogin(){
                this.isLoginUser =  localStorage.getItem('isLoginUser');
                console.log('login user fjdkfjdafjlj',this.isLoginUser)
            },
            logout(){
                this.clear_localStorage();
                // window.location.href = "/admin/login"
                // window.location.href="/"
                this.$router.push({path:'/'})
            },
            clear_localStorage(){
                localStorage.removeItem('token');
                localStorage.removeItem('isLoginUser');
                localStorage.removeItem('username');
                console.log("localstoreg logout ",localStorage.getItem('isLoginUser'))
            }
        }

   
})

  
</script>

