<template>
    <div class="product container p-3 mx-auto">
         <div class="row pt-5">
                <div class="col-md-3 d-none d-md-block">
                    <img class="logo" src="@/assets/KWorkTech_new_logo.jpg">
                </div>
          </div>
          <div class="row border-top border-bottom border-dark font-weight-bold ">
                <app-sm-side-nav class="d-block d-md-none pl-3"></app-sm-side-nav>
                 <div class="offset-md-3 p-2 pl-4 text-uppercase" >
                    Create subcategory
                  </div> 
                  <div class="offset-md-4 p-2">
                    <header-options 
                            v-bind:in_data="headerOptions">
                    </header-options>
                </div>
          </div>
        <div class="row">
            <div class="col-md-3">
                <app-side-nav class="d-none d-md-block pt-3" :categoryList1="categoryList"></app-side-nav>
            </div>
            <div class="col-md-9 pt-5">
                <section>
                    <div class="row pt-5">
                        <div class="col p-1 border subcategory-container">
                            <div class="col-12 col-md-12 pt-3 category">
                                <label class="col-12 col-md-6"> CATEGORY</label> 
                                <select class="col-md-6" v-model="AddSubcategory.main_category_id">
                                    <option :value="undefined" selected disabled>Select the Category</option>
                                    <option v-for="(item,index) in categoryList" :value="item.main_category_id" :key="index">{{ item.main_category_name }}</option>
                                </select>
                            </div>
                            <div class="col-12 col-md-12">
                                <label class="col-12 col-md-6"> SUB CATEGORY NAME</label>
                                <input v-model="AddSubcategory.sub_category_name" placeholder="Enter here..">
                            </div>
                        </div>
                    </div>
                </section>
                 <footer class="p-3 text-center">
                    <button  class="btn btn-primary" data-toggle="modal" data-target="#isAddSubCategory">Add Sub Category</button>
                </footer>
                <div class="modal fade" id="isAddSubCategory" role="dialog">
                    <div class="modal-dialog modal-md">
                        <div class="modal-content">
                            <div class="modal-header">
                                <button type="button" class="close" data-dismiss="modal">&times;</button>
                                <h4 class="modal-title">Add new Sub category</h4>
                            </div>
                            <div class="modal-body">
                                <p class="body-text"> Would you like to add new sub category?</p>
                            </div>
                            <div class="modal-footer">
                                <button type="button" class="btn btn-default"  data-dismiss="modal">No</button>
                                <button type="button" class="btn btn-primary" @click="createSubCategory()" data-dismiss="modal">YES</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div> 
            
        </div>
       
    </div>
</template>

<script lang="ts">
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    // Import one of available themes
    import 'vue-toast-notification/dist/theme-default.css';
    //import 'vue-toast-notification/dist/theme-sugar.css';
    

    import AddCategoriesService from '../../shared/services/addCategory.service'
    import ProductService from '../../shared/services/product.service'

    import sideNav from '../../components/layout/side-nav.vue'
    import smSideNav from '../../components/layout/sm-side-nav.vue';
    import HeaderOption from '../../components/common/Header-options.vue'

    Vue.use(VueToast)
    const addCategoriesService = new AddCategoriesService();
    const productService = new ProductService();

    // const sidenav = new sideNav();

    export default Vue.extend ({ 
        
    components : {
        'app-side-nav':sideNav,
        'app-sm-side-nav':smSideNav,
        'header-options':HeaderOption
    },
         data() {
            return {
                toast:Vue.$toast,
                productList:{},
                categoryList:{},
                AddSubcategory:{},
                sc_id:0,
                mc_id:0,
                headerOptions: {
                    createProduct:'true',
                    createCategory:'true',
                    createSubcategory:'false',
                    createBrand:'true'
                }
            }
        },
        methods: {
            getCategory() {
                productService.getAdminCategoryList()
                .then(response => {
                    this.categoryList = response.data.output.success[0].data
                    this.categoryList.forEach((element, index) => {
                        var tempObj = JSON.parse(this.categoryList[index].sub_category);
                        this.subcategoriesList[element['sub_category']]=[];
                            for(var i in tempObj){
                                var obj = {};
                                obj['subcategory_id'] = i;
                                obj['subcategory_name'] = tempObj[i];
                                this.subcategoriesList[element['sub_category']].push(obj);
                            }
                        this.categoryList[index]['sub_category']=this.subcategoriesList[element['sub_category']]
                    }); 
                })
                .catch(e => {
                this.errors.push(e)
                })
            },
            createSubCategory(){
                let _subcategoryForm = new FormData()
                    Object.keys(this.AddSubcategory).forEach((key) => {
                        _subcategoryForm.append(key, this.AddSubcategory[key])
                    })
                addCategoriesService.createSubCategory(_subcategoryForm)
                .then(response => {
                   console.log(response);
                   this.AddSubcategory = {};
                    // this.getCategory();
                   
                    this.toast.success("New sub category is added",{
                        position:'top-right',
                        width:'120px'
                   })
                    this.$router.go()
                    // this.getCategory()
                })
                .catch(e => {
                    this.toast.error("Error occured while adding",{
                        position:'top-right'
                   })
                this.errors.push(e)
                })

            },
        },
         created() {
             this.getCategory();
            // productService.getProductList(this.mc_id,this.sc_id)
            // .then(response => {
            // this.productList = response.data.output.success[0].data;
            // console.log("this.productDEtails product details",this.productList)
            // })
            // .catch(e => {
            // this.errors.push(e)
            // })  
        }   
    })
</script>

<style  scoped>
  @import '../../style.css';


   .header-bg-image{
       height: 400px !important;
   }

   .table td, .table th {
        padding: .75rem;
        vertical-align: top;
        border: 1px solid #e5e5e5;
   }

   .closebtn {
        position: absolute;
        top: 0;
        right: 55px;
        font-size: 55px;
        margin-left: 50px;
        cursor: pointer;
    }
    .logo{
        width:50%;
    }
    select{
            background: white;
    border: none;
    border-bottom: 1px solid #858585;
    }
    input {
        line-height: 30px;
    }
    .subcategory-container{

        line-height: 50px;

    }
   
</style>