<template>
    <div class="product container p-3 mx-auto">
        <!-- <div class="row"> -->
            <div class="row pt-5">
                <div class="col-md-3 d-none d-md-block">
                    <img class="logo" src="@/assets/KWorkTech_new_logo.jpg">
                </div>
          </div>
          <div class="row border-top border-bottom border-dark font-weight-bold ">
                <app-sm-side-nav class="d-block d-md-none pl-3"></app-sm-side-nav>
                 <div class="offset-md-3 p-2 pl-4 text-uppercase" >
                    Create Brands
                  </div> 
                  <div class="offset-md-5 p-2">
                    <header-options 
                            v-bind:in_data="headerOptions">
                    </header-options>
                </div>
          </div>
            <div class="row">
                <div class="col-md-3">
                    <app-side-nav class="d-none d-md-block pt-3"></app-side-nav>
                </div>
                <div class="col-md-9 container-body">
                    <section>
                        <div class="row pt-5">
                            <div class="col p-1 border brand-container">
                                <div class="col-12 col-md-12 pt-3">
                                    <label class="col-md-6"> BRANDS NAME </label>
                                    <input v-model="addBrands.brand_name" placeholder="Enter here..">
                                </div>
                            </div>
                        </div>
                    </section>
                    <footer class="p-3 text-center">
                        <button data-toggle="modal" class="btn btn-primary" data-target="#isAddBrand">Add Brands</button>
                    </footer>
                    <div class="modal fade" id="isAddBrand" role="dialog">
                        <div class="modal-dialog modal-md">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <button type="button" class="close" data-dismiss="modal">&times;</button>
                                    <h4 class="modal-title">Add new Brands</h4>
                                </div>
                                <div class="modal-body">
                                    <p class="body-text"> Would you like to add new brand?</p>
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-default"  data-dismiss="modal">No</button>
                                    <button type="button" class="btn btn-primary" @click="createBrands()" data-dismiss="modal">YES</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div> 
            
        <!-- </div> -->
       
    </div>
</template>

<script lang="ts">
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    // Import one of available themes
    import 'vue-toast-notification/dist/theme-default.css';
    //import 'vue-toast-notification/dist/theme-sugar.css';
 
    import AddCategoriesService from '../../shared/services/addCategory.service'

    import sideNav from '../../components/layout/side-nav.vue'
    import smSideNav from '../../components/layout/sm-side-nav.vue';
    // import HeaderOption from '@/pages/admin/Header-options'
    import HeaderOption from '@/components/common/Header-options'

    const addCategoriesService = new AddCategoriesService();
    Vue.use(VueToast)

    export default Vue.extend ({ 
    components : {
        'app-side-nav':sideNav,
        'app-sm-side-nav':smSideNav,
        'header-options':HeaderOption
    },
         data() {
            return {
                toast:Vue.$toast,
                productList:{},
                addBrands:{},
                sc_id:0,
                mc_id:0,
                headerOptions: {
                    createProduct:'true',
                    createCategory:'true',
                    createSubcategory:'true',
                    createBrand:'false'
                }
            }
        },
        methods: {           
            createBrands(){
                let _brandsForm = new FormData()
                    Object.keys(this.addBrands).forEach((key) => {
                        _brandsForm.append(key, this.addBrands[key])
                    })
                addCategoriesService.createBrands(_brandsForm)
                .then(response => {
                    this.addBrands={};
                   console.log("response is reponse is",response);
                    this.toast.success("New brand is added",{
                        position:'top-right'
                    })
                    // this.$router.go();
                })
                .catch(e => {
                    this.toast.error("Error occured while adding",{
                        position:'top-right'
                    })  
                     this.errors.push(e)
                })

            }
        },
         created() {
         }
    })
</script>

<style  scoped>
  @import '../../style.css';

   .header-bg-image{
       height: 400px !important;
   }

   .table td, .table th {
        padding: .75rem;
        vertical-align: top;
        border: 1px solid #e5e5e5;
   }

   .closebtn {
        position: absolute;
        top: 0;
        right: 55px;
        font-size: 55px;
        margin-left: 50px;
        cursor: pointer;
    }
     .container-body{
        overflow-y: auto;
        /* height: fit-content; */
        /* height: 700px; */
        overflow-x: hidden;
    }
    .logo{
        width:50%;
    }
    .brand-container{
        line-height: 50px;
    }
    input{
        line-height: 30px;
    }
   
</style>